
'use client';

import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Check, Star } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { usePageBackground } from '@/hooks/usePageBackground';
import { Badge } from '@/components/ui/badge';

const RazorpaySubscriptionButton = () => {
  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://cdn.razorpay.com/static/widget/subscription-button.js';
    script.async = true;
    script.setAttribute('data-subscription_button_id', 'pl_RDsiJqCv0SUHNV');
    script.setAttribute('data-button_theme', 'brand-color');
    
    const form = document.getElementById('razorpay-form');
    if (form) {
      while (form.firstChild) {
        form.removeChild(form.firstChild);
      }
      form.appendChild(script);
    }

    return () => {
      if (form && form.contains(script)) {
        try {
          form.removeChild(script);
        } catch (e) {
          // This can fail if the script is already gone, which is fine.
        }
      }
    }
  }, []);

  return (
    <div className="razorpay-container w-full">
      <style jsx global>{`
        .razorpay-container .razorpay-subscription-button {
          width: 100% !important;
          font-size: 1rem !important;
          padding: 1.25rem 2rem !important;
          border-radius: var(--radius);
        }
        .razorpay-container .razorpay-subscription-button-label-container,
        .razorpay-container .razorpay-subscription-button-container-outer {
            display: none !important;
        }
      `}</style>
      <div id="razorpay-form" className="w-full flex justify-center"></div>
    </div>
  );
};


